<style lang="scss" scoped>
$grid-template: 5% 13% 11% 10% 18% 13% 14% 5% 7% 4%;

@import "./src/styles/app";
.orders {
  font-family: Assistant;
  font-style: normal;
  padding-top: 23px;
  padding-inline-start: 83px;
  padding-inline-end: 91px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
