          font-weight: 700;
          color: $c-orange-15;
        }
      }
    }

  }

  &--actions {
    display: flex;
    align-items: center;

    .orders--sort {
      font-weight: 400;
      font-size: 18px;
      line-height: 24px;
      color: #6A707E;
      margin-inline-end: 25px;
    }

  }

  &--content {
    height: 100%;
    overflow: scroll;
    margin-top: 20px;

    .orders--table {
      position: relative;
      background: #FFFFFF;
      box-shadow: 0px 0px 4px rgba(171, 171, 171, 0.25);
      border-radius: 4px;
      font-family: Assistant;
      font-style: normal;
      padding-top: 22px;
      padding-bottom: 85px;
      padding-inline-end: 40px;

      .grid--row {
        display: grid;
        grid-template-columns: $grid-template;
      }

      .table--head {
        margin-bottom: 16px;
      }
    ;

      .table--head .table--cell {
        font-weight: 600;
        font-size: 16px;
        line-height: 21px;
        letter-spacing: 0.01em;
        color: $c-grey-90;
      }

      .table--row {
        border-bottom: 1px solid #F0F0F0;
        padding: 30px 0;

        .table--cell {
          font-family: Rubik;
          font-style: normal;
          font-weight: 300;
          font-size: 16px;
          line-height: 19px;
          letter-spacing: 0.01em;
          color: $c-grey-blue-55;

          &.name {
            font-family: Assistant;
            font-weight: 600;
          }
        }

        &:last-child {
          border-bottom: none;
        }

        .progress--cell {
          display: flex;
          align-items: center;

          .progress--block {
            margin-inline-end: 8px;
          }
        }

        .radio--cell {
          text-align: end;
        }

        .table--radio {
          margin-inline-end: 24px;
          visibility: hidden;
          position: relative;

          &:before {
            box-sizing: border-box;
            width: 20px;
            height: 20px;
            border-radius: 50%;
            position: relative;
            background-color: #FFFFFF;
            border: 1px solid $c-grey-38;
            content: '';
            display: inline-block;
            visibility: visible;
          }

          &:checked:after {
            width: 12px;
            height: 12px;
            border-radius: 50%;
            top: 4px;
            left: -3px;
            position: absolute;
            z-index: 3;
            background-color: $c-orange-15;
            content: '';
            display: inline-block;
            visibility: visible;
          }
        }
