<template>
  <div class="status--bar">
    <div :style="{'width': random + '%'}" class="progress"></div>
  </div>
</template>

<script>
export default {
name: "StatusBar",
  props:{
    value: {
      type: Number,
      // default:10
    }

  },
  computed:{
    random(){
      return Math.floor(Math.random() * 100) + 1
    }
  }
}
</script>

<style lang="scss" scoped>
.status--bar{
  width: 70px;
  height: 8px;
  background: #F1F1F1;
  border-radius: 13px;
  .progress{
    height: 100%;

    background-color: red;
    border-radius: 13px;
  }
}
</style>
