<template>
  <v-easy-dialog
    v-model="open"
    name="my-first-modal"
    :backgroundBtnTag='"div"'
    :backdropStyle='"background-color:rgba(43, 42, 40, 0.4)"'
    :width='"80vw"'
  >
    <div class="modal">
      <div class="modal--head">
        <div class="modal--number">Order 1242314</div>
        <div class="modal--close">
          <img @click="setModal({name:'orderModal',value:false})" src="@/assets/icons/close.svg" alt="" srcset="">
        </div>
      </div>
      <div class="modal--content">
        <div class="content--info">
          <div class="info--block">
            <div class="info">
              <div class="info--data">
                <h3 class="info--client">Client</h3>
                <div class="info--client--name">John Smith</div>
              </div>
              <div class="info--data">
                <h3 class="info--client">Data</h3>
                <div class="info--client--date">20-09-2020   11:30</div>
              </div>
            </div>
            <div class="info">
              <div class="info--data address">
                <h3 class="info--client">Address</h3>
                <div class="info--client--date">Main street 23</div>
              </div>
              <div class="info--data">
                <img src="@/assets/map.png" alt="">
              </div>
            </div>
          </div>
          <div class="info--block">
            <div class="info photos--block">
              <div class="info--data">
                <h3 class="info--client">Photos</h3>
              </div>
              <div class="info--data">
                <photo-picker :name="'first'">Photo of check</photo-picker>
              </div>
              <div class="info--data">
                <photo-picker :name="'second'">Photo of check</photo-picker>
              </div>
              <div class="info--data">
                <photo-picker :name="'third'">Photo of check</photo-picker>
              </div>
            </div>
            <div class="info photos--block">
              <div class="info--data">
                <photo-picker :name="'2'">Photo of check</photo-picker>
              </div>
              <div class="info--data">
                <photo-picker :name="'3'">Photo of check</photo-picker>
              </div>
              <div class="info--data">
              <photo-picker :name="'4'">Photo of check</photo-picker>
            </div>
            </div>
          </div>
        </div>
        <div class="content--list">
          <div class="orders--list">
            <div class="orders--head"></div>
            <div class="orders--head">Name product</div>
            <div class="orders--head">Comment</div>
            <div class="orders--head">Price</div>
            <div class="orders--head">Photo from home</div>
          </div>
          <div v-for="i in 3" :key="i" class="orders--list item--list">
            <div class="orders--info number">1</div>
            <div class="orders--info name">Gold Chair</div>
            <div class="orders--info"> mauris mauris  Pharetra  mauris non in ante amet, egestas id.</div>
            <div class="orders--info price">
              <span class="orders--price">250$</span>
              <buttons-action
                  :edit="true"
                  :trash="true"
              />
            </div>
            <div class="orders--info">
              <photo-picker :preview="false">Add</photo-picker>
            </div>
          </div>
          <div class="modal--total">
            <div class="total--price">250$</div>
            <div class="total--text">Total price:</div>
          </div>
        </div>
      </div>

    </div>
  </v-easy-dialog>
</template>
<script>

import VEasyDialog from 'v-easy-dialog'
import PhotoPicker from "@/components/Orders/PhotoPicker";
import ButtonsAction from "@/components/Orders/ButtonsAction";
import {mapMutations, mapState} from 'vuex'
export default {
  name:"OrderModal",
  components:{
    ButtonsAction,
    PhotoPicker,
    VEasyDialog
  },
  methods:{
    ...mapMutations(['setModal'])
  },
  computed:{
    ...mapState(['orderModal']),
    open:{
      get(){
        return this.orderModal
      },
      set(value){
        this.setModal({name:'orderModal', value})
      }
    }
  }
}
</script>
<style  lang="scss">
@import './src/styles/app';

.v-easy-dialog--content-container{
  background-color: #FFFFFF;
  border-radius: 4px;
}
.modal{
  padding: 46px 96px;
  position: relative;

  &--head{

    .modal--number{
      font-family: Rubik;
      font-style: normal;
      font-weight: 500;
      font-size: 28px;
      line-height: 33px;
      letter-spacing: 0.01em;
      color: $c-grey-90;
    }

    .modal--close{
      position: absolute;
      left: 35px;
      top: 30px
    }
  }

  &--content{
    display: grid;
    grid-template-columns: 525px auto;
    font-family: Rubik;
    font-style: normal;
    margin-top: 27px;

    .content--info{
      display: flex;
      flex-direction: column;
      padding-left: 40px;
      border-left: 1px solid $c-grey-40;


      .info--data:not(:last-child){
        margin-bottom: 22px;

        &.address{
          margin-bottom: 10px;
        }
      }
      .info--block{
        display: flex;
        justify-content: space-between;
        &:not(:last-child){
          margin-bottom: 16px;
        }
      }

      .info--client{
        font-weight: 500;
        font-size: 18px;
        line-height: 21px;
        letter-spacing: 0.01em;
        margin-top: 0;
        margin-bottom: 8px;
        color: $c-grey-90;

        &--name{
          font-weight: normal;
          font-size: 14px;
          line-height: 17px;
          letter-spacing: 0.01em;

          color: $c-orange-15;
        }
        &--date{
          font-size: 14px;
          line-height: 17px;
          color: rgba(155, 155, 155, 0.5);
        }
      }
      & .photos--block:last-child{
        margin-top: 40px;

      }
    }


    .content--list{

      padding-inline-start: 64px;

      .orders--list{
        display: grid;
        grid-template-columns: 40px 150px 300px 125px 130px;
        &.item--list{
          padding-bottom: 43px;
          border-bottom:  1px solid $c-grey-45;
          margin-top: 19px;
          .orders--info{
            font-family: Rubik;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 17px;
            text-align: right;
            letter-spacing: 0.01em;
            color: rgba(155, 155, 155, 0.5);

            &.name{
              color: $c-orange-15;
            }

            &.price{
              display: flex;
              align-items: center;

            }
            .orders--price{
              margin-inline-end: 15px;
            }
          }
        }
      }

      .orders--head{
        font-family: Rubik;
        font-style: normal;
        font-weight: 500;
        font-size: 11px;
        line-height: 13px;
        text-align: right;
        letter-spacing: 0.01em;
        color: #BDBDBD;
      }
    }
  }
  .modal--total{
    display: flex;
    justify-content: flex-end;
    font-family: Rubik;
    margin-top: 15px;
    letter-spacing: 0.01em;
    .total--text{
      font-size: 14px;
      line-height: 17px;
      color: #BDBDBD;
    }
    .total--price{
      font-size: 18px;
      line-height: 21px;
      color: #828282;
    }
  }

}
</style>
