import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from "@/views/Login";
import Dashboard from "@/views/Dashboard";
import Orders from "@/views/Orders";
import Customers from "@/views/Customers";
import Products from "@/views/Products";
import Service from "@/views/Service";
import AddData from "@/views/AddData";

Vue.use(VueRouter)

const routes = [
    {
        path: '/login',
        name: 'Login',
        component: Login,
        meta: {
            auth: false,
            layout: 'Auth'
        }
    },
    {
        path: '/',
        name: 'Dashboard',
        component: Dashboard,
        meta: {
            auth: false,
            layout: 'Main'
        }
    },
    {
        path: '/orders',
        name: 'Orders',
        component: Orders,
        meta: {
            auth: false,
            layout: 'Main'
        }
    },
    {
        path: '/customers',
        name: 'Customers',
        component: Customers,
        meta: {
            auth: false,
            layout: 'Main'
        }
    },
    {
        path: '/products',
        name: 'Products',
        component: Products,
        meta: {
            auth: false,
            layout: 'Main'
        }
    },
    {
        path: '/service',
        name: 'Service',
        component: Service,
        meta: {
            auth: false,
            layout: 'Main'
        }
    },
    {
        path: '/gallery',
        name: 'Gallery',
        component: Dashboard,
        meta: {
            auth: false,
            layout: 'Main'
        }
    },
    {
        path: '/add',
        name: 'Add Data',
        component: AddData,
        meta: {
            auth: false,
            layout: 'Main'
        }
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
