import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        orderModal: false,
        customerModal: false,
        productModal: false,
        productCard: false
    },
    mutations: {
        setModal(state, payload) {
            state[payload.name] = payload.value
        }
    },
    actions: {},
    getters: {},
    modules: {}
})
