<template>
<div class="products">
  <app-header @addProduct="addProduct" add-button>Add new product</app-header>
  <search-bar @inputValue="searchValue"/>
  <div class="products--wrap">
    <div class="products--content">
      <!--    TODO test for visual-->
      <ProductCard @click.native="openCard" v-for="i in 10" :key="i"/>
    </div>
  </div>
  <add-product-modal/>
  <product-card-modal/>
</div>
</template>

<script>
import AppHeader from "@/components/App/AppHeader";
import SearchBar from "@/components/App/SearchBar";
import ProductCard from "@/components/Products/ProductCard";
import AddProductModal from "@/components/Products/productModal/AddProductModal";
import {mapMutations} from 'vuex'
import ProductCardModal from "@/components/Products/productModal/ProductCardModal";
export default {
name: "Products",
  components: {ProductCardModal, AddProductModal, ProductCard, SearchBar, AppHeader},
  data(){
    return {
      searchValue: ''
    }
  },
  methods:{
  ...mapMutations(['setModal']),
  openCard(){
    this.setModal({name: 'productCard', value: true})
  },
    addProduct(){
      this.setModal({name: 'productModal', value: true})
    }
  }
}
</script>

<style lang="scss" scoped>
.products{
  padding: 23px 73px 23px 101px;
  height: 100%;
  box-sizing: border-box;
  &--wrap{

  }
  &--content{
    padding-top: 20px;
    display: grid;
    grid-template-columns: repeat(5,1fr);
    grid-gap: 20px;
    overflow: scroll;
  }
}
</style>
