<template>
  <div class="picker">
    <label class="file--input--label" :for="name">
      <img class="file--input--icon" src="@/assets/icons/+.svg" alt="">
      <slot></slot>
    </label>
    <input type="file" @change="loadIMG" ref="fileLoader" class="file--input" :id="name"  accept=".jpg, .jpeg, .png">
    <img v-if="preview" class="preview--image" src="@/assets/Rectangle 17.png" alt="" :ref="name">
  </div>

</template>

<script>
export default {
name: "PhotoPicker",
  props:{
    name: {
      type: String,
      default: Date.now()+''
    },
    preview:{
      type: Boolean,
      default: true
    }
  },
  data(){
    return {
      hasImage: false
    }
  },
  methods:{
    loadIMG(e){
      let file = e.target.files[0] || e.dataTransfer.files[0]
      let output = this.$refs[this.name]
      let reader = new FileReader();
      reader.onload = function () {
        output.src = reader.result;
      };
      reader.readAsDataURL(file);
      this.hasImage = true
    }
  },
  created() {


  }
}
</script>

<style lang="scss" scoped>
.picker{
  position: relative;
  display: flex;
  align-items: center;
}
.file--input{
  padding: 6px 15px;
  box-sizing: border-box;
  max-width: 145px;
  visibility: hidden;
  &::-webkit-file-upload-button{
    display: none;
  }

  &--icon{
    margin-inline-start: 15px;
    margin-inline-end: 11px;
  }

  &--label{
    position: absolute;
    max-width: 145px;
    box-sizing: border-box;
    padding: 4px 0 4px 32px;
    font-family: Assistant;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    display: flex;
    align-items: center;
    text-align: right;
    letter-spacing: 0.01em;
    color: #F2950A;
    background: rgba(255, 176, 57, 0.08);
    border-radius: 4px;
  }

}
.preview--image{
  width: 38px;
  height: 36px;
  border-radius: 4px;
  margin-inline-start: 20px;
}

</style>
