<template>
  <div class="searchbar">
    <input v-model="search" class="searchbar--input" type="text" :placeholder="name">
    <img class="searchbar--icon" src="@/assets/icons/Search.svg" alt="">
  </div>
</template>

<script>
export default {
  name: "SearchBar",
  props:{
    name:{
      type: String,
      default: ' '
    }
  },
  data(){
    return {
      searchString: null
    }
  },

  computed:{
    search:{
      get(){
        return this.searchString
      },
      set(value){
        this.searchString = value
        this.$emit('inputValue', this.searchString)
      }
    }
  }

}
</script>

<style lang="scss" scoped>
@import "./src/styles/app";
.searchbar{
  background: #FFFFFF;
  box-shadow: 0px 0px 4px rgba(171, 171, 171, 0.4);
  border-radius: 2px;
  position: relative;

  &--input{
    width: 100%;
    box-sizing: border-box;
    padding: 13px 0;
    padding-inline-start: 30px;
    padding-inline-end: 50px;
    font-family: Assistant;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    border: none;

    &:focus{
    outline: none;
    }

    &::placeholder{
      color: $c-grey-45;
    }
  }
  &--icon{
    position: absolute;
    left: 20px;
    top: 16px;
  }
}
</style>
