<template>
  <div class="radial">
    <apexchart type="radialBar" height="280" :options="chartOptions" :series="series"></apexchart>
    <div class="radial--legend">
      <div class="legend--item">
        <div class="legend--count">150</div>
        <div class="legend--color"></div>
        <div class="legend--description">Successful</div>
      </div>
      <div class="legend--item">
        <div class="legend--count">34</div>
        <div class="legend--color unsuccess"></div>
        <div class="legend--description">Unsuccessful</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "RadialBar",
  data() {
    return {
      series: [64],
      chartOptions: {
        chart: {
          height: 150,
          type: 'radialBar',
        },
        colors: ["#F2950A"],
        plotOptions: {
          radialBar: {
            hollow: {
              size: '80%'
            },
            dataLabels: {
              name: {
                show: false
              },
              value: {
                offsetY: -2,
                fontSize: '18px',
                fontFamily: 'Rubik, sans-serif',
                fontWeight: 'normal',
                colors: '#333333'
              }
            }
          }
        },
        stroke: {
          lineCap: 'round'
        }
      },
    }
  }
}
</script>

<style lang="scss" scoped>
.radial--legend{
  display: flex;
  justify-content: space-around;
}
.legend--item{
  display: grid;
  font-family: Rubik;
  font-style: normal;
  grid-template-columns: 0fr 1fr;
  grid-template-rows: 2fr 1fr;
  grid-template-areas:
      "count color"
      "descr descr";
  align-items: center;
  .legend--count{
    grid-area: count;
    font-weight: normal;
    font-size: 32px;
    line-height: 38px;
    display: flex;
    align-items: center;
    color: #333333;
  }
  .legend--color{
    grid-area: color;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #F2950A;
    margin-inline-start: 9px;
    &.unsuccess{
      background-color: rgba(255, 176, 57, 0.1);
    }
  }
  .legend--description{
    grid-area: descr;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    display: flex;
    align-items: center;
    color: #828282;
  }
}
</style>
