<template>
  <apexchart type="bar" height="350" :options="chartOptions" :series="series"></apexchart>
</template>

<script>
export default {
name: "SalesBigChart",
  data() {
    return {
      series: [{
        name: 'test',
        data: [75000, 125000, 100000, 175000, 200000],
        color: '#f2950a'
      }],
      chartOptions: {
        chart: {
          type: 'bar',
          height: 320,
          animations: {
            enabled: false
          },
          toolbar:{
            show: false
          }
        },
        plotOptions: {
          bar: {
            horizontal: false,
            columnWidth: '14px',
            endingShape: 'rounded'

          },
        },
        dataLabels: {
          enabled: false
        },
        stroke: {
          show: true,
          width: 2,
          colors: ['transparent']
        },
        xaxis: {
          categories: ['Mar 1 - 7', 'Mar 8 - 14', 'Mar 15 - 21', 'Mar 22 - 28', 'Final wk'],
          labels: {
            show: true,
            style: {
              colors: '#BDBDBD',
              fontSize: '16px',
              fontFamily: 'Rubik, sans-serif',
              fontWeight: 400,
              cssClass: 'apexcharts-xaxis-label',
            }
          },
        },
        yaxis: {

          labels: {
            style: {
              colors: '#BDBDBD',
              fontSize: '16px',
              fontFamily: 'Rubik, sans-serif',
              fontWeight: 400,
              cssClass: 'apexcharts-xaxis-label',
            },
            offsetX: -20,
            offsetY: 0,
          },
        },
        fill: {
          opacity: 1
        },
        tooltip: {
          y: {
            formatter: function (val) {
              return "$ " + val
            }
          }
        }
      }
    }
  }
}
</script>

