<template>
  <v-easy-dialog
      v-model="open"
      name="productModal"
      :backgroundBtnTag='"div"'
      :backdropStyle='"background-color:rgba(43, 42, 40, 0.4)"'
      :width='"40vw"'
  >
    <div class="modal product">
      <div class="modal--head">
        <div class="modal--title">Add new product</div>
        <div class="modal--close">
          <img @click="setModal({name:'productModal',value:false})" src="@/assets/icons/close.svg" alt="" srcset="">
        </div>
      </div>
      <div class="modal--content content--production">
        <div class="content--block">
          <div class="content--wrap">
            <span class="id--number">432423432</span>
            <span class="id--name">ID</span>
          </div>
          <div class="content--wrap">
            <p class="prod--name">Name of the product</p>
            <input class="modal--input" type="text">
          </div>
          <div class="content--wrap">
            <p class="prod--name">Amount</p>
            <input class="modal--input" type="text">
          </div>
        </div>
        <div class="content--block">
          <div class="content--wrap">
           <Sort/>
          </div>
          <div class="content--wrap">
            <p class="prod--name">Name of the product</p>
            <input class="modal--input" type="text">
          </div>
          <div class="content--wrap">
            <p class="prod--name">Amount</p>
            <input class="modal--input" type="text">
          </div>
        </div>
      </div>
      <photo-picker/>
      <div class="modal--button">
        <Button>Save</Button>
      </div>
    </div>
  </v-easy-dialog>
</template>
<script>

import VEasyDialog from 'v-easy-dialog'
import {mapMutations, mapState} from 'vuex'
import Sort from "@/components/Orders/Sort";
import Button from "@/components/_shared/Button";
import PhotoPicker from "@/components/Products/PhotoPicker";
export default {
  name:"AddProductModal",
  components:{
    PhotoPicker,
    Button,
    Sort,
    VEasyDialog
  },
  methods:{
    ...mapMutations(['setModal'])
  },
  computed:{
    ...mapState(['productModal']),
    open:{
      get(){
        return this.productModal
      },
      set(value){
        this.setModal({name:'productModal', value})
      }
    }
  }
}
</script>
<style  lang="scss">
@import './src/styles/app';

.v-easy-dialog--content-container{
  background-color: #FFFFFF;
  border-radius: 4px;
}
.modal{
  padding: 43px 96px;
  position: relative;
  font-family: Rubik;
  font-style: normal;
  &.product{
    padding-inline-start: 131px;
    padding-inline-end: 90px;
    padding-bottom: 23px;
  }

  &--head{
    .modal--title{
      font-weight: 500;
      font-size: 36px;
      line-height: 47px;
      letter-spacing: 0.01em;
      color: $c-grey-90;
      text-align: center;
    }

    .modal--close{
      position: absolute;
      left: 35px;
      top: 30px
    }
  }

  &--content{
    font-family: Rubik;
    font-style: normal;
    letter-spacing: 0.01em;
    &.content--production{
      display: flex;
      justify-content: space-between;
    }
    .modal--input{
      background: #FFFFFF;
      border: 0.5px solid rgba(0, 0, 0, 0.1);
      box-sizing: border-box;
      border-radius: 4px;
      padding: 9px 5px;
      width: 255px;

      &:focus{
        outline: none;
      }
    }

    .id--name{
      font-size: 16px;
      line-height: 19px;
      color: $c-grey-90;
      margin-inline-end: 7px;
    }
    .id--number{
      font-size: 16px;
      line-height: 19px;
      color: rgba(155, 155, 155, 0.5);
    }

    .prod--name{
      font-size: 16px;
      line-height: 19px;
      text-align: right;
      letter-spacing: 0.01em;
      color: $c-grey-90;
      margin-top: 27px;
    }
  }
  .modal--button{
    width: 203px;
    margin: 10px auto;

  }
}
</style>

